import React from 'react';
import '../Styles/LightModeSwitcher.css';
import {gsap} from 'gsap';

let isAnimationActive = false;

const onClick = () => {
  if (!isAnimationActive) {
    const isLightModeActive = toggleLightMode();
    animateSwitcherDot(isLightModeActive);
  }
};
const toggleLightMode = () => {
  const root = document.documentElement;
  root.classList.toggle('lightMode');
  const isLightModeActive = root.classList.contains('lightMode');
  return isLightModeActive;
};

const animateSwitcherDot = (isLightModeActive) => {
  const switcherWidth = document.querySelector('.switcher').clientWidth;
  const switcherDotWidth = document.querySelector('.switcherDot').clientWidth;
  if (isLightModeActive) {
    isAnimationActive = true;
    const tl = gsap.timeline();
    tl.to('.switcherDot', {
      width: (switcherWidth / 3) * 2,
      height: 10,
      duration: 0.2,
    });
    tl.to('.switcherDot', {
      width: switcherDotWidth,
      height: switcherDotWidth,
      x: switcherWidth - switcherDotWidth,
      ease: 'bounce.out',
      duration: 0.2,
      onComplete: () => (isAnimationActive = false),
    });
  } else if (!isLightModeActive) {
    isAnimationActive = true;
    const tl = gsap.timeline();
    tl.to('.switcherDot', {
      width: (switcherWidth / 3) * 2,
      height: switcherDotWidth / 2,
      x: switcherWidth - (switcherWidth / 3) * 2,
      duration: 0.2,
    });
    tl.to('.switcherDot', {
      width: switcherDotWidth,
      height: switcherDotWidth,
      x: 0,
      duration: 0.2,
      ease: 'bounce.out',
      onComplete: () => (isAnimationActive = false),
    });
  }
};

const LightModeSwitcher = () => {
  return (
    <button className='switcher'>
      <div className='switcherDot' onClick={onClick}></div>
    </button>
  );
};

export default LightModeSwitcher;
