import React from 'react';
import SectionTitle from './SectionTitle';
import Form from './Form';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faAt, faPhone} from '@fortawesome/free-solid-svg-icons';
import {
  faFacebook,
  faGithub,
  faLinkedinIn,
} from '@fortawesome/free-brands-svg-icons';
import '../Styles/Contact.css';

function Contact() {
  const contactList = [
    {
      platform: 'facebook',
      link: 'https://www.facebook.com/mateusz.brek',
      icon: faFacebook,
    },
    {
      platform: 'github',
      link: 'https://github.com/Matezbreniu/',
      icon: faGithub,
    },
    {
      platform: 'linkedin',
      link: 'https://www.linkedin.com/in/mateusz-brek/',
      icon: faLinkedinIn,
    },
    {
      platform: 'e-mail',
      link: 'mailto:matezbreniu@gmail.com',
      icon: faAt,
    },
    {
      platform: 'telephone',
      link: 'tel:+48783469216',
      icon: faPhone,
    },
  ];

  return (
    <section className='contact' id='contact'>
      <SectionTitle title='Kontakt' id='Contact' />
      <div className='wrapper'>
        <div className='contactSectionContainer'>
          <Form />
          <div className='contactContainer'>
            {contactList.map((contact) => {
              return (
                <a
                  href={contact.link}
                  key={contact.platform}
                  className='contactItem swipeFromLeft'
                >
                  <FontAwesomeIcon
                    style={{width: '100%', height: '100%'}}
                    className='contactIcon'
                    icon={contact.icon}
                  />
                </a>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
}
export default Contact;
