import React, {Component} from 'react';
import emailjs from 'emailjs-com';
import '../Styles/Form.css';

class Form extends Component {
  state = {
    email: '',
    subject: '',
    message: '',
    isValid: false,

    errors: {
      email: false,
      subject: false,
      message: false,
    },
  };

  backgroundColorForError = {};
  backgroundColorForValidate = {};

  onChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({
      [name]: value,
    });
  };

  onSubmit = (e) => {
    e.preventDefault();
    const validation = this.formValidation();
    this.setStyle();
    if (validation.correct) {
      const params = {
        email: this.state.email,
        subject: this.state.subject,
        message: this.state.message,
      };
      emailjs.send(
        'gmail',
        'template_me5ngmG9',
        params,
        'user_96YAId7zE22lFOv4GkM10'
      );
    }
    this.setState({
      isValid: validation.correct,
      errors: {
        email: !validation.email,
        subject: !validation.subject,
        message: !validation.message,
      },
    });
  };

  setStyle = () => {
    this.backgroundColorForValidate = {
      backgroundColor: 'rgba(81, 248, 81, 0.87)',
    };
    this.backgroundColorForError = {
      backgroundColor: 'rgba(248, 81, 81, 0.87)',
    };
  };

  formValidation = () => {
    let correct = false;
    let email = false;
    let subject = false;
    let message = false;
    if (
      this.state.email.indexOf('@') > 0 &&
      this.state.email.indexOf('.') > 0
    ) {
      email = true;
    }
    if (this.state.subject.length > 1) {
      subject = true;
    }
    if (this.state.message.length > 1) {
      message = true;
    }
    if (email && subject && message) {
      correct = true;
    }
    return {correct, email, subject, message};
  };

  render() {
    return (
      <form className='form' onSubmit={this.onSubmit}>
        <label className='formLabel' htmlFor='email'>
          <input
            type='text'
            className='formInput'
            style={
              this.state.errors.email
                ? this.backgroundColorForError
                : this.backgroundColorForValidate
            }
            id='email'
            placeholder='E-mail'
            value={this.state.email}
            onChange={this.onChange}
            name='email'
          />
        </label>
        <label className='formLabel' htmlFor='subject'>
          <input
            type='text'
            className='formInput'
            style={
              this.state.errors.subject
                ? this.backgroundColorForError
                : this.backgroundColorForValidate
            }
            id='subject'
            placeholder='Temat'
            value={this.state.subject}
            onChange={this.onChange}
            name='subject'
          />
        </label>
        <label className='formLabel' htmlFor='message'>
          <textarea
            className='formInput'
            style={
              this.state.errors.message
                ? this.backgroundColorForError
                : this.backgroundColorForValidate
            }
            id='message'
            placeholder='Twoja wiadomość'
            value={this.state.message}
            onChange={this.onChange}
            name='message'
          />
        </label>
        <button className='formButton'>Wyślij</button>
        {this.state.isValid ? (
          <div className='formSuccess'>
            <p className='formSuccessText'>Dziękuję za kontakt!</p>
          </div>
        ) : null}
      </form>
    );
  }
}

export default Form;
