import React, {Component} from 'react';
import {gsap} from 'gsap';
import '../Styles/Header.css';

class Header extends Component {
  state = {
    clickedLetter: '',
  };
  keyboardKeys = [
    ['Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P'],
    ['A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L'],
    ['Z', 'X', 'C', ' ', 'V', 'B', 'N', 'M'],
  ];

  highlightedStyles = {
    backgroundColor: '#2e94cf',
    boxShadow: 'none',
    transform: 'translate(4px,4px)',
  };

  setHeaderText = () => {
    document.querySelector('.headerTitle').textContent = ' ';
    document.querySelector('.headerSubTitle').textContent = ' ';
  };

  highlightClickedLetter = (letter) => {
    if (letter === 'ę') {
      letter = 'e';
    }
    const clickedLetter = letter.toUpperCase();
    this.setState({
      clickedLetter,
    });
  };

  typeWriteAnimation = () => {
    gsap.to('.headerCursorSpan', {
      color: 'transparent',
      yoyo: true,
      repeat: -1,
      ease: 'none',
      duration: 0.2,
      repeatDelay: 0.3,
    });
  };

  typeWrite = (text, className) => {
    const headerClassName = document.querySelector(`.headerText ${className}`);
    const headerCursorSpan = document.querySelector('.headerCursorSpan');
    const headerTextSpan = document.createElement('span');
    headerTextSpan.className = 'headerTextSpan';
    headerClassName.appendChild(headerTextSpan);
    headerClassName.appendChild(headerCursorSpan);
    const splittedText = text.split('');
    splittedText.forEach((letter, index) => {
      setTimeout(() => {
        headerTextSpan.textContent += letter;
        this.highlightClickedLetter(letter);
      }, index * 250);
    });
    if (className !== '.headerSubTitle') {
      setTimeout(() => {
        this.typeWrite('Frontend Developer!', '.headerSubTitle');
      }, splittedText.length * 250);
    }
  };

  componentDidMount() {
    this.setHeaderText();
    this.typeWriteAnimation();
    this.typeWrite('Mateusz Bręk', '.headerTitle');
  }

  render() {
    return (
      <header className='header' id='header'>
        <div className='headerText'>
          <h1 className='headerTitle'>Mateusz Bręk</h1>
          <h2 className='headerSubTitle'>Front-end Developer</h2>
          <span className='headerCursorSpan'>_</span>
        </div>
        <div className='headerKeyboard'>
          {this.keyboardKeys.map((row) => {
            return (
              <div key={row} className='keyboardRow'>
                {row.map((letter) => {
                  return (
                    <div
                      className='keyboardLetter'
                      key={letter}
                      style={
                        this.state.clickedLetter === letter
                          ? this.highlightedStyles
                          : null
                      }
                    >
                      {letter}
                    </div>
                  );
                })}
              </div>
            );
          })}
        </div>
      </header>
    );
  }
}

export default Header;
