import React, {Component} from 'react';
import AboutMe from './AboutMe';
import Projects from './Projects';
import Skills from './Skills';
import Contact from './Contact';
import {gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';

class Main extends Component {
  setAnimationPosition = () => {
    gsap.set('.swipeFromLeft', {
      x: '-50%',
      opacity: 0,
    });
    gsap.set('.swipeFromRight', {
      x: '50%',
      opacity: 0,
    });
    gsap.set('.swipeFromBottom', {
      y: '100px',
      opacity: 0,
    });
    gsap.set('.fadeIn', {
      opacity: 0,
    });
  };

  titleAnimation = () => {
    const titleList = document.querySelectorAll('.title');
    titleList.forEach((title) => {
      const tl = gsap.timeline({
        scrollTrigger: {
          trigger: `#${title.id}`,
          start: 'bottom 80%',
          toggleActions: 'play none play none',
        },
      });
      tl.to(`#${title.id}`, {
        x: 0,
        duration: 0.5,
        opacity: 1,
      });
      tl.to(
        `#${title.id}~.bar`,
        {
          x: 0,
          duration: 0.5,
          opacity: 1,
        },
        0.2
      );
      return null;
    });
  };
  propertyAnimation = () => {
    const propertyContainerList = document.querySelectorAll(
      '.propertyContainer'
    );
    propertyContainerList.forEach((propertyContainer, index) => {
      const propertyIcon = propertyContainer.childNodes[0].childNodes[0];
      const timeDelay = index * 0.2;
      const tl = gsap.timeline({
        scrollTrigger: {
          trigger: '.propertyContainer',
          start: 'bottom 80%',
          toggleActions: 'play none play none',
        },
      });
      tl.to(
        propertyContainer,
        {
          x: 0,
          opacity: 1,
        },
        timeDelay
      );
      tl.to(propertyIcon, {
        opacity: 1,
      });

      return null;
    });
  };
  contactAnimation = () => {
    gsap.to('.contactItem', {
      scrollTrigger: {
        trigger: '.contactItem',
        start: 'bottom 90%',
        toggleActions: 'play none play none',
      },
      x: 0,
      opacity: 1,
      ease: 'elastic.out(0.5, 0.5)',
      duration: 1,
      stagger: 0.2,
    });
  };
  aboutMeImageAnimation = () => {
    gsap.to('.aboutMeImage', {
      scrollTrigger: {
        trigger: '.aboutMeImage',
        start: 'bottom 80%',
        toggleActions: 'play none play none',
      },
      x: 0,
      opacity: 1,
    });
  };
  aboutMeInfoAnimation = () => {
    gsap.to('.aboutMeInfoContainer', {
      scrollTrigger: {
        trigger: '.aboutMeInfoContainer',
        start: 'bottom 80%',
        toggleActions: 'play none play none',
      },
      x: 0,
      opacity: 1,
    });
  };
  projectAnimation = () => {
    const projectList = document.querySelectorAll('.project');
    projectList.forEach((project) => {
      gsap.to(project, {
        scrollTrigger: {
          trigger: project,
          start: 'bottom 120%',
          toggleActions: 'play none play none',
        },
        y: 0,
        duration: 0.5,
        opacity: 1,
      });
    });
  };
  skillAnimation = () => {
    gsap.to('.skill', {
      scrollTrigger: {
        trigger: '.skillsContainer',
        start: 'bottom 100%',
        toggleActions: 'play none play none',
      },
      y: -25,
      opacity: 1,
      ease: 'elastic.out(0.8, 0.5)',
      duration: 1.5,
      stagger: 0.2,
    });
  };

  componentDidMount() {
    gsap.registerPlugin(ScrollTrigger);
    this.setAnimationPosition();

    //Animations
    this.titleAnimation();
    this.propertyAnimation();
    this.contactAnimation();
    this.aboutMeImageAnimation();
    this.aboutMeInfoAnimation();
    this.projectAnimation();
    this.skillAnimation();
  }
  render() {
    return (
      <main>
        <AboutMe />
        <Projects />
        <Skills />
        <Contact />
      </main>
    );
  }
}
export default Main;
