import React from 'react';
import SectionTitle from './SectionTitle';
import Project from './Project';
import SportRegeneracjaBackground from '../Images/sportregeneracjaBackground.jpg';
import SwiezaskibkaBackground from '../Images/swiezaskibkaBackground.jpg';
import SquareGameBackground from '../Images/squareGameBackground.jpg';

import '../Styles/Projects.css';

function Projects() {
  const projectsList = [
    {
      name: 'Weather App',
      class: 'weatherApp',
      description: 'Aplikacja pogodowa z wykorzystaniem API',
      technology: 'React.js',
      siteLink: 'https://matezbreniu.github.io/weatherapp/',
      repositoryLink: 'https://github.com/Matezbreniu/WeatherApp',
    },
    {
      name: 'Sportregeneracja.com',
      class: 'sportregeneracja',
      description: 'Strona projektu sportowej regeneracji',
      technology: 'HTML/CSS/JS',
      siteLink: 'https://www.sportregeneracja.com',
      background: SportRegeneracjaBackground,
    },
    {
      name: 'Swiezaskibka.pl',
      class: 'swiezaskibka',
      description: 'Formularz zamówień dla dostawcy pieczywa',
      technology: 'React.js',
      siteLink: 'https://www.swiezaskibka.pl',
      repositoryLink: 'https://github.com/Matezbreniu/swiezaskibka.pl',
      background: SwiezaskibkaBackground,
    },
    {
      name: 'SquareGame',
      class: 'squareGame',
      description: 'Gra stworzona w canvas',
      technology: 'HTML/CSS/JS',
      siteLink: 'https://matezbreniu.github.io/squareGame/',
      repositoryLink: 'https://github.com/Matezbreniu/squareGame',
      background: SquareGameBackground,
    },
  ];
  return (
    <section className='projects' id='projects'>
      <div className='wrapper'>
        <SectionTitle title='Projekty' id='Projects' />
        <div className='projectsContainer'>
          {projectsList.map((project) => (
            <Project key={project.name} project={project} />
          ))}
        </div>
      </div>
    </section>
  );
}
export default Projects;
